import './home.css';
import circle from './circle1.png';
import photo1 from './photo.png';

function Home() {
  return (
    <div className="home">
      <div className="text">
        <span className="title">  </span>
        <p className="bold">
               <br /> 
        </p>
        <p className="normal">
                  .
        </p>
        <a
          href="https://docs.google.com/forms/d/e/1FAIpQLSeY5_C0ghzP49GfmHn7cLHXh5MxvQqa3wdcXJEKe4Zc-G__nA/viewform?pli=1"
          target="_blank"
          rel="noopener noreferrer"
        >
          <button className="btn2">
            <img src={circle} alt="icon" />
            <span> </span>
          </button>
        </a>
      </div>
      <img src={photo1} alt="decoration" className="photo1" />
    </div>
  );
}

export default Home;

