import './button.css';
import temps from './temps.png';
import check from './check.png';
import medical from './medical-symbol.png';

function Button() {
  return (
    <div className="Buttons">
      <div className="button">
        <img src={temps} alt="icon" className="icon3" />
        <div className="text-button">
          <p className="button-title">4  </p>
          <p className="desc">   </p>
        </div>
      </div>

      <div className="button">
        <img src={check} alt="icon" className="icon3" />
        <div className="text-button">
          <p className="button-title">  50 </p>
          <p className="desc">     </p>
        </div>
      </div>

      <div className="button">
        <img src={medical} alt="icon" className="icon3" />
        <div className="text-button">
          <p className="button-title">   </p>
          <p className="desc">     </p>
        </div>
      </div>
    </div>
  );
}

export default Button;


